import Sequelize from 'sequelize'
import cloneDeep from 'lodash/cloneDeep'
import get from 'lodash/get'
import map from 'lodash/map'
import each from 'lodash/each'
import size from 'lodash/size'
import genPm from 'wsemi/src/genPm.mjs'
import genID from 'wsemi/src/genID.mjs'
import isarr from 'wsemi/src/isarr.mjs'
import isobj from 'wsemi/src/isobj.mjs'
import isbol from 'wsemi/src/isbol.mjs'
import strright from 'wsemi/src/strright.mjs'
import pmSeries from 'wsemi/src/pmSeries.mjs'
import importModels from './importModels.mjs'


/**
 * (Microsoft SQL)
 *
 * @class
 * @param {Object} [opt={}] {}
 * @param {String} [opt.url='mssql://localhost:1433'] 'mssql://localhost:1433'
 * @param {String} [opt.db='worm'] 'worm'
 * @param {String} [opt.cl='test'] 'test'
 * @param {String} [opt.fdModels='models'] models(*.js)'models'
 * @param {Boolean} [opt.logging=false] sqlfalse
 * @param {String} [opt.pk='id'] 'id'
 * @param {Boolean} [opt.autoGenPK=true] pk(id)uuidtrue
 * @returns {Object} 
 */
function WOrm(opt = {}) {


    //default
    if (!opt.url) {
        opt.url = 'mssql://localhost:1433'
    }
    if (!opt.db) {
        opt.db = 'worm'
    }
    if (!opt.cl) {
        opt.cl = 'test'
    }
    if (!opt.fdModels) {
        opt.fdModels = 'models'
    }
    if (strright(opt.url, 1) !== '/') {
        opt.url += '/' + opt.db
    }
    opt.logging = (opt.logging === true)
    if (!opt.pk) {
        opt.pk = 'id'
    }
    if (!isbol(opt.autoGenPK)) {
        opt.autoGenPK = true
    }


    //sequelize
    let sequelize = new Sequelize(opt.url, {
        define: {
            timestamps: false
        },
        logging: opt.logging,
    })


    //mds
    let mds = importModels(opt.fdModels, sequelize)


    //Op
    let Op = Sequelize.Op
    //console.log('Op', Op)


    /**
     * 
     *
     * @memberOf WOrm
     * @param {Object} [find={}] 
     * @returns {Promise} Promiseresolvereject
     */
    async function select(find = {}) {

        function cvObj(o) {
            let oNew = {}
            each(o, (v, k) => {
                let kNew = k
                if (k.indexOf('$') >= 0) {
                    k = k.replace('$', '')
                    if (k === 'regex') {
                        kNew = Op.substring
                    }
                    else if (k === 'options') {
                        kNew = null
                    }
                    else if (k === 'nin') {
                        kNew = Op.notIn
                    }
                    else {
                        kNew = Op[k]
                    }
                }
                let vNew = v
                if (isarr(v)) {
                    vNew = cvArray(v)
                }
                else if (isobj(v)) {
                    vNew = cvObj(v)
                }
                if (kNew !== null) {
                    oNew[kNew] = vNew
                }
            })
            return oNew
        }

        function cvArray(o) {
            let oNew = []
            each(o, (v) => {
                let vNew = v
                if (isarr(v)) {
                    vNew = cvArray(v)
                }
                else if (isobj(v)) {
                    vNew = cvObj(v)
                }
                oNew.push(vNew)
            })
            return oNew
        }

        function cvFind(o) {
            let oNew = {}
            if (isobj(o)) {
                oNew = cvObj(o)
            }
            else {
                console.log('select: find is not object')
            }
            return oNew
        }

        //useFind
        let useFind = cvFind(find)

        //md
        let md = mds[opt.cl]

        //findAll
        let rs = await md.findAll({
            where: useFind,
            raw: true,
        })

        return rs
    }


    /**
     * _idinsert
     *
     * @memberOf WOrm
     * @param {Object|Array} data 
     * @returns {Promise} Promiseresolvereject
     */
    async function insert(data) {

        //cloneDeep
        data = cloneDeep(data)

        //pm
        let pm = genPm()

        //md
        let md = mds[opt.cl]

        //check
        if (!isarr(data)) {
            data = [data]
        }

        //check
        if (opt.autoGenPK) {
            data = map(data, function(v) {
                if (!v[opt.pk]) {
                    v[opt.pk] = genID()
                }
                return v
            })
        }

        //bulkCreate
        md.bulkCreate(data)
            .then((res) => {
                //console.log('bulkCreate then',res)
                pm.resolve({ n: size(data), ok: 1 })
            })
            .catch(({ original }) => {
                //console.log('bulkCreate catch',original)
                pm.reject({ n: 0, ok: 0 })
            })

        return pm
    }


    /**
     * 
     *
     * @memberOf WOrm
     * @param {Object|Array} data 
     * @param {Object} [option={}] {}
     * @param {boolean} [option.autoInsert=true] true
     * @param {boolean} [option.atomic=false] false
     * @returns {Promise} Promiseresolvereject
     */
    async function save(data, option = {}) {

        //cloneDeep
        data = cloneDeep(data)

        //autoInsert, atomic
        let autoInsert = get(option, 'autoInsert', true)
        let atomic = get(option, 'atomic', false)

        //pm
        let pm = genPm()

        //md
        let md = mds[opt.cl]

        //check
        if (!isarr(data)) {
            data = [data]
        }

        //check
        if (opt.autoGenPK) {
            data = map(data, function(v) {
                if (!v[opt.pk]) {
                    v[opt.pk] = genID()
                }
                return v
            })
        }

        //tr
        let t = null
        let tr = {}
        if (atomic) {
            t = await sequelize.transaction()
            tr = {
                transaction: t
            }
        }

        //pmSeries
        pmSeries(data, async function(v) {
            let pmm = genPm()

            //err
            let err = null

            //r
            let r
            if (v[opt.pk]) {
                //id

                //findOne
                r = await md.findOne({
                    where: { [opt.pk]: v[opt.pk] },
                    raw: true,
                })
                    .catch((error) => {
                        err = error
                    })

            }
            else {
                //id
                err = `${opt.pk} is invalid`
            }

            if (r) {
                //

                let rr = await md.update(v, {
                    where: { [opt.pk]: v[opt.pk] },
                    ...tr,
                })
                    .catch((error) => {
                        err = error
                    })

                if (rr) {
                    //console.log('update ', rr)
                    pmm.resolve({ n: 1, nModified: 1, ok: 1 })
                }
                else {
                    //console.log('update ', err)
                    pmm.resolve({ n: 0, nInserted: 0, ok: 1 })
                }

            }
            else {
                //

                //autoInsert
                if (autoInsert) {

                    //create
                    let rr = await md.create(v, tr)
                        .catch((error) => {
                            err = error
                        })

                    if (rr) {
                        //console.log('create ', rr)
                        pmm.resolve({ n: 1, nInserted: 1, ok: 1 })
                    }
                    else {
                        //console.log('create ', err)
                        pmm.resolve({ n: 0, nInserted: 0, ok: 1 })
                    }

                }
                else {
                    //console.log('findOne ', err)
                    pmm.resolve({ n: 0, nModified: 0, ok: 1 })
                }

            }

            pmm._err = err //eslint
            return pmm
        })
            .then((res) => {
                pm.resolve(res)
                if (t) {
                    //console.log('transaction commit')
                    return t.commit()
                }
            })
            .catch((res) => {
                pm.reject(res)
                if (t) {
                    //console.log('transaction rollback')
                    return t.rollback()
                }
            })

        return pm
    }


    /**
     * 
     *
     * @memberOf WOrm
     * @param {Object|Array} data 
     * @returns {Promise} Promiseresolvereject
     */
    async function del(data) {

        //cloneDeep
        data = cloneDeep(data)

        //pm
        let pm = genPm()

        //md
        let md = mds[opt.cl]

        //check
        if (!isarr(data)) {
            data = [data]
        }

        //pmSeries
        pmSeries(data, async function(v) {
            let pmm = genPm()

            //err
            let err = null

            //r
            let r
            if (v[opt.pk]) {
                //id

                //findOne
                r = await md.findOne({
                    where: { [opt.pk]: v[opt.pk] },
                    raw: true,
                })
                    .catch((error) => {
                        err = error
                    })

            }
            else {
                //id
                err = `${opt.pk} is invalid`
            }

            if (r) {
                //

                //destroy
                let rr = await md.destroy({
                    where: { [opt.pk]: v[opt.pk] },
                })
                    .catch((error) => {
                        err = error
                    })

                if (rr) {
                    //console.log('destroy ', rr)
                    pmm.resolve({ n: 1, nDeleted: 1, ok: 1 })
                }
                else {
                    //console.log('destroy ', err)
                    pmm.resolve({ n: 0, nDeleted: 0, ok: 1 })
                }

            }
            else {
                //console.log('findOne ', err)
                pmm.resolve({ n: 1, nDeleted: 1, ok: 1 })
            }

            pmm._err = err //eslint
            return pmm
        })
            .then((res) => {
                pm.resolve(res)
            })
            .catch((res) => {
                pm.reject(res)
            })

        return pm
    }


    /**
     * del
     *
     * @memberOf WOrm
     * @param {Object} [find={}] 
     * @returns {Promise} Promiseresolvereject
     */
    async function delAll(find = {}) {

        //pm
        let pm = genPm()

        //md
        let md = mds[opt.cl]

        //destroy
        md.destroy({
            where: find,
        })
            .then((res) => {
                pm.resolve({ n: res, ok: 1 })
            })
            .catch((res) => {
                pm.reject({ n: 0, ok: 1 })
            })

        return pm
    }


    return {
        select,
        insert,
        save,
        del,
        delAll,
    }
}


export default WOrm
